import React, { Component } from 'react';
import styled from 'styled-components'
import './App.scss';

let redt: string = "translate(30vw,-32vh)";
let bh1t: string = "translateX(-200vw)";
let bv1t: string = "translateY(200vh)";
let bluet: string = "translateY(20vh)";
let bh2t: string = "translateX(-200vw)";
let bv2t: string = "translateX(100vw)";
let bh3t: string = "translateY(-300vh)";
let yellowt: string = "translateX(-8vw)";
let scrollY: number = 0;
let winY: number = window.innerHeight;
let m3op: number = 0;
let progress: number = 0;
let progressnow: number = 0;
let Red: any = styled.div`transform:${redt}`;
let BlackH1: any = styled.div`transform:${bh1t}`;
let BlackV1: any = styled.div`transform:${bv1t}`;
let Blue: any = styled.div`transform:${bluet}`;
let BlackV2: any = styled.div`transform:${bv2t}`;
let BlackH2: any = styled.div`transform:${bh2t}`;
let BlackH3: any = styled.div`transform:${bh3t}`;
let Yellow: any = styled.div`transform:${yellowt}`;
let M2hidden: any = styled.div`opacity:${m3op}`;

class App extends Component {
  constructor(props: any) {
    super(props);
    window.addEventListener("resize", this.resizing);
    window.addEventListener("scroll", this.scrolling);
  }
  componentWillUnmount() {

    window.removeEventListener("resize", this.resizing);
    window.removeEventListener("scroll", this.scrolling);
  }

  resizing() {
    winY = window.innerHeight;
  }

  scrolling = () => {
    scrollY = document.documentElement.scrollTop;
    progress = Math.round(scrollY / winY * 100);
    requestAnimationFrame(() => {
      this.ani();
    });
  }

  ani = () => {
    const red: any = document.querySelector(".red")
    const blackH1: any = document.querySelector(".blackH1")
    const blackV1: any = document.querySelector(".blackV1")
    const blue: any = document.querySelector(".blue")
    const blackV2: any = document.querySelector(".blackV2")
    const blackH2: any = document.querySelector(".blackH2")
    const blackH3: any = document.querySelector(".blackH3")
    const yellow: any = document.querySelector(".yellow")
    const m2hidden: any = document.querySelectorAll(".m2hidden")
    if (progress - progressnow > 0) {
      progressnow++;
      let p = Math.min(progressnow / 100, 1)
      redt = `translate(${30 - p * 30}vw,-${32 - p * 32}vh)`;
      bh1t = `translateX(-${200 - p * 200}vw)`;
      bv1t = `translateY(${200 - p * 200}vh)`;
      bluet = `translateY(${20 - p * 20}vh)`;
      bh2t = `translateX(-${200 - p * 200}vw)`;
      bv2t = `translateX(${100 - p * 100}vw)`;
      bh3t = `translateY(-${300 - p * 300}vh)`;
      yellowt = `translateX(-${8 - p * 8}vw)`;
      m3op = Math.max((progressnow - 100) / 15, 0);

      red.style.transform = `${redt}`;
      blackH1.style.transform = `${bh1t}`;
      blackV1.style.transform = `${bv1t}`;
      blue.style.transform = `${bluet}`;
      blackV2.style.transform = `${bv2t}`;
      blackH2.style.transform = `${bh2t}`;
      blackH3.style.transform = `${bh3t}`;
      yellow.style.transform = `${yellowt}`;
      m2hidden.forEach((item :any) => {
        item.style.opacity = `${m3op}`;
      });

      if (progress !== progressnow) {
        requestAnimationFrame(() => {
          this.ani();
        });
      }
    } else if (progress - progressnow < 0) {
      progressnow--;
      let p = Math.min(progressnow / 100, 1)
      redt = `translate(${30 - p * 30}vw,-${32 - p * 32}vh)`;
      bh1t = `translateX(-${200 - p * 200}vw)`;
      bv1t = `translateY(${200 - p * 200}vh)`;
      bluet = `translateY(${20 - p * 20}vh)`;
      bh2t = `translateX(-${200 - p * 200}vw)`;
      bv2t = `translateX(${100 - p * 100}vw)`;
      bh3t = `translateY(-${300 - p * 300}vh)`;
      yellowt = `translateX(-${8 - p * 8}vw)`;
      m3op = Math.max((progressnow - 100) / 15, 0);

      red.style.transform = `${redt}`;
      blackH1.style.transform = `${bh1t}`;
      blackV1.style.transform = `${bv1t}`;
      blue.style.transform = `${bluet}`;
      blackV2.style.transform = `${bv2t}`;
      blackH2.style.transform = `${bh2t}`;
      blackH3.style.transform = `${bh3t}`;
      yellow.style.transform = `${yellowt}`;
      m2hidden.forEach((item :any) => {
        item.style.opacity = `${m3op}`;
      });

      if (progress !== progressnow) {
        requestAnimationFrame(() => {
          this.ani();
        });
      }
    }
  }


  render() {
    return (
      <>
        <div className="main1">
          <h1>UI 개발 인턴 <br /> 프론트 UI 블로그 </h1>
          <h3>1/12 부터 교육 예정</h3>
          <h5>react / typescript</h5>
        </div>
        <div className="main2">
          <Red className="red"><M2hidden className="m2hidden">UI인턴 소개<i className="fas fa-sign-in-alt"></i></M2hidden> </Red>
          <BlackH1 className="blackH1" />
          <BlackV1 className="blackV1" />
          <Blue className="blue"><M2hidden className="m2hidden">UI 컴포넌트<i className="fas fa-sign-in-alt"></i> </M2hidden></Blue>
          <BlackH2 className="blackH2" />
          <BlackV2 className="blackV2" />
          <BlackH3 className="blackH3" />
          <Yellow className="yellow" ><M2hidden className="m2hidden">WebGL<i className="fas fa-sign-in-alt"></i> </M2hidden></Yellow>
        </div>
        <div className="main3">
        </div>
      </>
    );
  }
}

export default App;