import React, { Component } from 'react';
import './Navi.scss';
import { Link } from 'react-router-dom';
import LOGO from './imgs/mainLOGOdark.png';

class Navi extends Component {
  componentDidMount() {
    document.querySelector(".hamburg")!.addEventListener("click", () => {
      document.querySelector(".nav-items")!.classList.toggle("active");
      document.querySelector(".hamburg")!.classList.toggle("active");
    });
  }
  render() {
    return (
      <>
        <nav>
          <Link className="nav-brand" to="/"><img src={LOGO} alt="brand" /></Link>
          <div className="nav-items">
            <Link to="/" className="nav-a">홈</Link>
          </div>
          <div className="hamburg"><i className="fas fa-plus"></i></div>
        </nav>
      </>
    );
  }
}

export default Navi;